@tailwind base;
@tailwind components;
@tailwind utilities;

/* Glowing Text Animation */
@keyframes glow {
  0% {
    text-shadow: 0 0 5px #ec4848, 0 0 10px #ec484b, 0 0 15px #ec4848;
  }
  50% {
    text-shadow: 0 0 20px #ec4848, 0 0 30px #ec4899, 0 0 40px #ec4899;
  }
  100% {
    text-shadow: 0 0 5px #ec4899, 0 0 10px #ec4899, 0 0 15px #ec4899;
  }
}

.glowing-text {
  animation: glow 1.5s ease-in-out infinite;
}

/* Rain Animation */
@keyframes rain {
  0% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(100vh);
  }
}

.misty-rain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 0;
}

.misty-rain .raindrop {
  position: absolute;
  width: 2px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0.6);
  animation: rain 0.5s linear infinite;
  opacity: 0.8;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.6);
}

.misty-rain .raindrop:nth-child(even) {
  animation-duration: 0.7s;
}

.misty-rain .raindrop:nth-child(odd) {
  animation-duration: 0.6s;
}

/* Balloons Animation */
@keyframes float {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100vh);
  }
}

.animate-float {
  animation: float linear infinite;
}

/* Star Mist Animation */
@keyframes zigzag {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(10px, -10px);
  }
  50% {
    transform: translate(-10px, -20px);
  }
  75% {
    transform: translate(10px, -30px);
  }
  100% {
    transform: translate(0, -40px);
  }
}

.star-mist {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 0;
}

.star-mist .star {
  position: absolute;
  width: 5px;
  height: 5px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
  animation: zigzag 3s linear infinite;
}

.star-mist .star:nth-child(even) {
  animation-duration: 3.5s;
}

.star-mist .star:nth-child(odd) {
  animation-duration: 2.8s;
}